@import "~utils/variables";

.wrapper {
  padding-top: $navbarHeight;
  background: #f1f1f1;

  .main_container {
    background:
      linear-gradient(90deg,
        rgba(0, 0, 0, 0.5) 28.96%,
        rgba(13, 32, 35, 0.14) 74.9%),
      url(../../assets/images/careerPedia/Base.png) center/cover no-repeat,
      lightgray 50%;
    padding-top: 60px;
    padding-bottom: 60px;


    .first_part {
      display: flex;
      flex-direction: column;
      justify-content: center;

      .text_section {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 20px;
        color: #ffff;

        span {
          font-size: 50px;
          font-weight: 600;
        }

        p {
          font-size: 20px;
          line-height: 27px;
          text-align: left;
          margin-bottom: 50px;
        }

        @media screen and (max-width: 600px) {
          span {
            padding-top: 2em;
            font-size: 35px;
          }

          p {
            padding: 1em;
            font-size: 15px;
            text-align: center;
          }
        }
      }

      .search_section {
        display: flex;

        .search_container {
          display: flex;
          align-items: center;
          background-color: #ffff;
          width: 500px;
          border-radius: 10px;
          margin-top: 20px;
        }

        .search_button {
          background-color: #007bff;
          color: white;
          padding: 10px 20px;
          border: none;
          margin-right: 8px;
          border-radius: 5px;
          cursor: pointer;
          transition: background-color 0.3s ease;

          &:hover {
            background-color: #0056b3;
          }
        }

        input {
          width: 100%;
          padding: 10px;
          height: 50px;
          border: none;
          border-radius: 5px 0 0 5px;
          outline: none;
          transition: box-shadow 0.3s ease;
        }

        .searchIcon {
          font-size: 30px;
          margin-left: 10px;
        }

        select {
          width: 100;
          padding: 10px;
          border: 1px solid #ccc;
          border-radius: 5px;
        }
      }

      .options {
        display: flex;
        color: #ffff;
        gap: 10px;
        margin-top: 20px;
        text-align: center;

        p {
          margin-top: 0.3em;
        }

        div {
          background-color: rgba(62, 55, 55, 0.5);
          cursor: pointer;
          border-radius: 3px;
          padding: 5px 10px;
          margin-right: 5px;
        }
      }
    }
  }

  .second_part {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    background: #f1f1f1;

    .blogGrid {
      padding-bottom: 80px;
      padding-top: 80px;
    }

    .article_cards {
      display: flex;
      flex-wrap: wrap;
      border-radius: 20px !important;
      background-color: white;
      box-shadow: 0px 15px 25px 0px #0000000f;

      .article_card {
        text-decoration: none;
        color: inherit;

        img {
          width: 100%;
          height: 200px;
          object-fit: cover;
          border-radius: 10px 10px 0 0;
        }

        .card_details {
          padding: 20px;

          h2 {
            font-weight: 500;
            margin-bottom: 12px;
            font-size: 22px;
          }

          h3 {
            margin: 0;
          }

          p {
            margin-top: 5px;
            font-size: 16px;
            margin-bottom: 5px;
            line-height: 1.7;
          }

          .authorSection {
            margin-top: 20px;
            display: flex;
            align-items: center;

            div {
              display: flex;
              flex-direction: column;
            }

            .authorPhoto {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              margin-right: 10px;
            }

            .groupAuthorNameSection {
              display: flex;
              flex-direction: row;
              width: 100%;
              justify-content: space-between;

              .date {
                margin-top: 10px;
                margin-left: 30px;
              }

              .authorName {
                font-weight: 500;
              }

              .verified {
                color: #000;
                font-weight: 100;
                font-size: 12px;
                display: flex;
                align-items: center;
              }

              .icons {
                width: 16px;
                height: 16px;
                object-fit: contain;
                margin-right: 5px;
              }
            }
          }
        }
      }
    }
  }

  .more {
    display: flex;
    justify-content: center;
    padding-top: 5em;

    .More {
      color: #4b9bee;
      border-color: #4b9bee;
      text-align: center;
    }
  }

  .getTouch {
    margin-top: 190px;
    background: #111116;

    .wave_svg {
      width: 100%;
      height: auto;
      fill: #111116;
      margin-top: -50%;
    }

    .group {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: -101px;
      width: 100%;
    }

    .textempower {
      display: flex;
      align-items: center;
      color: #ffff;
      font-size: 25px;
      font-weight: 700;
      line-height: 48px;
      letter-spacing: 0.2px;

      @media screen and (max-width: 600px) {
        font-size: 20px;
        line-height: 20px;
      }
    }

    .textjoin {

      font-size: 14px;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: 0px;
      text-align: left;
      color: #5a7184;

      @media screen and (max-width: 600px) {
        font-size: 16px;
        line-height: 20px;
      }
    }

  }

  @media screen and (max-width: 5000px) {
    .search_container {
      max-width: 500px;
    }

    input {
      width: 230px;
    }

    .card {
      width: 45%;

      .cardnamediv {
        height: 100px;
      }
    }
  }

  // Media Query for 1100px (Tablet)
  @media screen and (max-width: 1100px) {
    .main_container {
      .first_part {

        .text_section {}

        .search_section {
          flex-direction: column;
        }
      }
    }


  }

  @media screen and (max-width: 860px) {
    .options {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      left: 0;
      margin-bottom: 50px;
    }

    .main_container {
      justify-content: center !important;

      .text_section {
        align-items: center;
      }
    }

  }

  // Media Query for 600px (Mobile)
  @media screen and (max-width: 600px) {
    .main_container {
      padding: 0 20px;

      .first_part {
        align-items: center; // Center the text in the first part

        .search_container {
          margin-top: 10px !important;
          max-width: 320px;
        }
      }
    }

    .more {
      padding-bottom: 1em;
    }

    .second_part {
      flex-direction: column;
      align-items: center;

      .article_card {
        width: 100%;
      }
    }


  }

  @media screen and (max-width: 355px) {
    .search_container {
      margin-top: 20px !important;
      max-width: 120px;
    }
  }
}